.transactionHistory {
  text-align: center;
  border-collapse: collapse;
  border: 1px solid grey;
  margin: auto;
  margin-top: 10px;
}

table {
  width: 505px;
  border: 1px solid lightslategray;
  margin: 50px auto;
}

th {
  width: 33%;
}

thead {
  background-color: #6ecdda;
}

tr {
  text-align: center;
}

tbody tr:nth-child(odd) {
  background-color: white;
}
tbody tr:nth-child(even) {
  background-color: whitesmoke;
}
